import React from 'react'

import './card.styles.css'
export const Card = (props) =>
    (
    <div className='card-container'>
        <img alt='monster' src={`https://robohash.org/${props.family.id}?set=set2&seize=180x180`}/>
    <h2>
        {
            props.family.name
        }
    </h2>
        <p>{props.family.email}</p>
    </div>
    )
